@import "./custom.scss";

.Portfolio_Container {
  @include flexRow;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  gap: 30px;
  text-align: center;
  padding: 0 20px;
}

@keyframes background__move {
  from {
    background-position: top;
  }

  to {
    background-position: bottom;
  }
}

.Portfolio__element {
  @include flexCol;
  align-items: center;
  background-size: cover;
  background-position: top;
  max-width: 250px;
  width: 100%;
  height: 200px;
  background-color: $color3;
  box-sizing: border-box;
  box-shadow: 0 0 5px $color2;
  border: 1px solid $color2;

  &:hover {
    animation-name: background__move;
    animation-duration: 5s;
    animation-delay: 0.2s;
    animation-fill-mode: both;
    animation-play-state: running;
    animation-iteration-count: 2;
    animation-timing-function: linear;
    animation-direction: alternate;
    border-color: $color1;
    box-shadow: 0 0 5px $color1;
  }

  & * {
    opacity: 0;
  }

  &:hover * {
    opacity: 1;
  }

  button {
    @include flexRow;
    align-items: center;
    @include btn;
    border: none;
    align-self: flex-end;
    margin-top: auto;
    margin-right: -15px;
    margin-bottom: -15px;

    svg {
      margin-left: 5px;
      margin-right: -7px;
      fill: $color4;
    }
  }
}

.element__overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: white;
  opacity: 0.8;
  /*chose different color*/
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 2;
}

.element__info__container {
  @include flexRow;
  @include background;
  background-position: center;
  position: fixed;
  top: 50%;
  left: 50%;
  width: fit-content;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  box-shadow: 0 0 5px $color2;
  border: 1px solid $color2;
  z-index: 3;
  width: fit-content;
  padding: 10px;
  background-color: $color4;

  & * {
    color: $color1;
    font-family: Orbitron-regular;
  }

  .left__Content {
    @include flexCol;
    justify-content: flex-end;
    align-items: center;
    width: 250px;
    height: 250px;

    .site__background {
      top: 30px;
      width: 226px;
      height: 126px;
      position: absolute;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: top;

      animation-name: background__move;
      animation-duration: 6s;
      animation-delay: 0.2s;
      animation-fill-mode: both;
      animation-play-state: running;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      animation-direction: alternate;

      .deviceFrame {
        position: absolute;
        top: -9px;
        left: -13px;
        @include background;
        background-size: contain;
        width: 250px;
        height: 200px;
      }

      .desktop__frame {
        top: -9px;
        left: -12px;
        background-image: url(../assets/desktop__frame.png);
      }

      .tablet__frame {
        top: -21px;
        left: -65px;
        background-image: url(../assets/tablet__frame.png);
      }

      .mobile__frame {
        top: -6px;
        left: -81px;
        background-image: url(../assets/mobile__frame.png);
      }
    }

    .desktop__BG__size {
      width: 226px;
      height: 126px;
    }

    .tablet__BG__size {
      width: 119px;
      height: 158px;
    }

    .mobile_BG_size {
      top: 20px;
      width: 88px;
      height: 188px;
    }

    .infoBTN {
      @include flexRow;
      justify-content: center;
      gap: 10px;

      button {
        border: none;
        background-color: transparent;

        // &:hover {
        // }
        &:focus {
          outline: none;
        }

        svg {
          height: 30px;
          width: 30px;
          fill: $color1;
        }
      }
    }
  }

  .right__Content {
    @include flexCol;
    width: fit-content;

    h3 {
      margin-top: 0;
      text-align: center;
    }

    .closeBTN {
      display: block;
      margin-left: auto;
    }

    ul {
      list-style: none;
      text-align: left;
    }

    a {
      @include btn;
      white-space: nowrap;
      display: block;
      width: fit-content;
      text-decoration: none;
      margin-right: -25px;
      margin-left: auto;

      svg {
        white-space: nowrap;
        fill: $color4;
        margin-right: -15px;
        vertical-align: middle;
        margin-left: 10px;
        margin-right: -8px;
        margin-bottom: 2px;
      }
    }

    .site {
      margin-top: auto;
      margin-bottom: -25px;
    }
  }
}

