@import "../css/custom.scss";

.photoContainer {
  @include flexRow;
  flex-wrap: wrap;
  max-width: 90%;
  width: fit-content;
  height: fit-content;
  // outline: 1px solid black;
  gap: 20px;
  justify-content: center;
  padding-bottom: 50px;
}

.photo {
  @include background;
  width: 250px;
  height: 200px;
  background-color: grey;
  box-shadow: 0 0 5px $color2;
  border: 1px solid $color2;
}
