@import "../css/custom.scss";

.skill_Container {
  display: grid;

  width: 100%;
  gap: 10px;

  .skill__Element {
    @include flexCol;
    width: fit-content;
    border: 1px solid black;
    p {
      text-align: center;
      font-family: Orbitron-Regular;
      margin: 0;
      font-size: 12px;
    }

    .skill__inner__container {
      @include flexRow;
      align-items: center;

      svg {
        width: 50px;
        height: 50px;
        align-self: center;
        margin-right: 10px;
      }

      ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li {
          @include flexRow;
          line-height: 20px;

          svg {
            height: 20px;
            width: 20px;
            margin: 0;
            padding: 0;
          }
        }
      }
    }
  }
}
